import React, { Component } from "react";

import "./index.scss";

import Head from "&/components/head";

import { Tabs, WhiteSpace } from 'antd-mobile';

import { connect } from "react-redux";
import { getppList } from "../../actions";
import {history}  from "~"

const tabs = [
    { title: '全部品牌' },
    { title: '女鞋' },
    { title: '男鞋' },
    { title: '运动' },
    { title: '户外' },
    { title: '童鞋童装' },
];

@connect(
    state => (
        {
            ppList: state.getIn(["data", 'ppList']),
        }
    )
)
class Classify extends Component {


    componentDidMount() {
        const { dispatch, ppList } = this.props;
        if (!ppList.length > 0) {
            dispatch(getppList({
                url: "/react/ppList",
            }))
        }
    }
    gotoSearch = () => {
        history.push("/search")
    }
    render() {
        return (
            <div className="classify">
                <Head title="商品分类" show={true} location={this.props.location}></Head>
                <div style={{ height: 565 }}>
                    <WhiteSpace />
                    <Tabs tabs={tabs}
                        tabBarPosition="left"
                        tabDirection="vertical"
                    >
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '565px', backgroundColor: '#fff' }}>
                            <ul className="pp">
                                {
                                    this.props.ppList.map((p, i) => {
                                        return (
                                            <li onClick={this.gotoSearch} key={i}>
                                                <img src={p.url} alt="" />
                                                <p>{p.name}</p>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                        </div>

                    </Tabs>
                    <WhiteSpace />
                </div>

            </div>
        )
    }
}


export default Classify